body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

 @-webkit-keyframes experience-node--enter {
  0% {
    width: 0px;
  } 100% {
    width: 180px;
  }
 }

@keyframes experience-node--enter {
  0% {
    width: 0px;
  } 100% {
    width: 180px;
  }
 }

@-webkit-keyframes skillbar--enter {
  0% {
    box-shadow: inset -1500px 0px white;
  } 100% {
    box-shadow: inset 0px 0px white;
  }
}

@keyframes skillbar--enter {
  0% {
    box-shadow: inset -1500px 0px white;
  } 100% {
    box-shadow: inset 0px 0px white;
  }
}

@-webkit-keyframes interest-pie--enter {
 0% {
   -webkit-transform: rotateY(90deg) rotateZ(-180deg);
           transform: rotateY(90deg) rotateZ(-180deg);
 } 100% {
   -webkit-transform: rotateY(0deg) rotateZ(0deg);
           transform: rotateY(0deg) rotateZ(0deg);
 }
}

@keyframes interest-pie--enter {
 0% {
   -webkit-transform: rotateY(90deg) rotateZ(-180deg);
           transform: rotateY(90deg) rotateZ(-180deg);
 } 100% {
   -webkit-transform: rotateY(0deg) rotateZ(0deg);
           transform: rotateY(0deg) rotateZ(0deg);
 }
}

body {
  font-family: 'Nanum Gothic', sans-serif;
  padding: 50px;
}

.App {
  position: relative;
  text-align: center;
  border: 1px solid gray;
  box-shadow: 2px 2px 6px black;
  padding: 50px 200px 50px 200px;
}

.NoteOnSide {
  position: absolute;
  font-family: monospace;
  -webkit-writing-mode: vertical-lr;
          writing-mode: vertical-lr;
  left: 10px;
  top: 40px;
}

.Avatar {
  width: 100px;
  height: 100px;
  border: 1px solid gray;
}

.Title {
  font-size: 24px;
  font-weight: bold;
  margin: 10px 0px 40px 0px;
}

.Subtitle {
  display: -webkit-flex;
  display: flex;
  font-size: 20px;
  margin: 10px auto;
  width: 600px;
}
.SubtitleLabel {
  -webkit-flex: 0 0 200px;
          flex: 0 0 200px;
  text-align: right;
}
.SubtitleContent {
  -webkit-flex: 1 1;
          flex: 1 1;
  text-align: left;
  padding-left: 10px;
}
.Subtitle a {
  text-decoration: none;
}

.Section {
  border-bottom: 2px dashed gray;
  margin: 100px 0px 50px 0px;
}

.Skillbar {
  color: lightgray;
  font-size: 18px;
  font-weight: bold;
  margin: 2px;
  padding: 3px;
  background: -webkit-linear-gradient(45deg, crimson 0%, blue 90%);
  background: linear-gradient(45deg, crimson 0%, blue 90%);
  text-align: left;
  -webkit-animation-name: skillbar--enter;
          animation-name: skillbar--enter;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

.Skillbar--animate {
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

.ExperienceNode {
  display: inline-block;
  position: relative;
  width: 180px;
  height: 100px;
  text-align: left;
}

.ExperienceNode__line {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 0px;
  height: 4px;
  background: #8151d1;
}

.ExperienceNode__line--animate {
  -webkit-animation-name: experience-node--enter;
          animation-name: experience-node--enter;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.ExperienceNode__node {
  position: absolute;
  top: -3px;
  left: -5px;
  width: 10px;
  height: 10px;
  background: black;
  border-radius: 100%;
}

.ExperienceNode__company {
  position: relative;
  font-weight: bold;
  -webkit-transform: translate(-16px, 94px) rotate(45deg);
          transform: translate(-16px, 94px) rotate(45deg);
}

.ExperienceNode__label {
  position: relative;
  right: 10px;
  top: 8px;
  -webkit-transform: translate(-20px, 80px) rotate(45deg);
          transform: translate(-20px, 80px) rotate(45deg);
}

.ExperienceNode__year {
  position: relative;
  right: 35px;
  bottom: 35px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.InterestPie__pie {
  -webkit-transform: rotateY(90deg) rotateZ(-180deg);
          transform: rotateY(90deg) rotateZ(-180deg);
}

.InterestPie__pie--animate {
  -webkit-animation-name: interest-pie--enter;
          animation-name: interest-pie--enter;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.lang {
  margin: 10px auto;
  text-align: right;
  width: 100px;
}

.lang img {
  vertical-align: -10px;
}

.Presentation {
  margin-top: 50px;
  text-align: justify;

}

